html {
  scroll-behavior: smooth;
  box-sizing: border-box;
  width: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  width: 100%;
  min-height: 100%;
  overflow-x: hidden;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  background-color: var(--c-secondary);
  color: var(--c-tertiary);
  line-height: 1.3;
  font-family: Calibre, "San Francisco", "SF Pro Text", -apple-system,
    system-ui, -apple-system, Segoe UI, Roboto, Ubuntu, Cantarell, Noto Sans, sans-serif, BlinkMacSystemFont, Roboto, "Helvetica Neue", "Segoe UI", Arial,
    sans-serif;
  font-family: var(--font-Calibre);
  font-size: 20px;
  font-size: var(--fs-xl);
}

.hidden {
  overflow: hidden;
}

/* App Scrollbar */

/* width */

::-webkit-scrollbar {
  width: 5px;
}

/* Track */

::-webkit-scrollbar-track {
  background: var(--c-secondary);
}

/* Handle */

::-webkit-scrollbar-thumb,
::-webkit-scrollbar-thumb:hover {
  background: var(--c-primary);
  border-radius: 5px;
}

